@use "mixins";

html {
  margin: 0;
  padding: 0;
  font-family: "Times New Roman", arial, serif;
  font-size: 14pt;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
}

.buttons {
  @include mixins.buttons;
  &:hover {
    cursor: pointer;
    background: rgb(189, 4, 38);
  }
}
